import React,{useState} from 'react';
import AddTask from '../Component/AddTask';
import Header from '../Component/Header';
import Tasks from '../Component/Tasks';

const TaskPage = () => {
  const [showAddTask, setShowAddTask] = useState(false)
  const [tasks,setTasks] = useState([
    {
    id:1,
    text:'fgnofcxvxd gfvdxf rgoir',
    day: "Feb 5th 2:50pm",
    reminder: false,
  },
  {
    id:2,
    text:'fgnofd rgoir',
    day: "Feb 5th 2:50pm",
    reminder: true,
  },
  {
    id:3,
    text:'fgnofd bfxd rgoir',
    day: "Feb 5th 2:50pm",
    reminder: true,
  },
  {
    id:4,
    text:'fgnofd gdfg rgoir',
    day: "Feb 5th 2:50pm",
    reminder: true,
  },
  {
    id:5,
    text:'fgdf fgnccbvcxkjofd rgoir',
    day: "Feb 5th 2:50pm",
    reminder: true,
  },
]);
 
//Delete Task
const deleteTask = (id) =>{
  setTasks(tasks.filter((task) => task.id !== id));

}

//toggle remainder

const toggleRemainder = (id) => {

 setTasks(tasks.map((task) => task.id == id ? {...task,  reminder: !task. reminder} : task))
}

//Add Task
const addtask = (task) =>{
  const id = Math.floor(Math.random() * 10000) + 1;
  const newTask = {id, ...task}
  setTasks([...tasks, newTask])
  console.log(task);
}

  return (<div className='container'>
      <Header onAdd={() => setShowAddTask(!showAddTask)} showAdd={!showAddTask}/>
     {showAddTask && <AddTask onAdd={addtask}/>}
     {tasks.length > 0 ? (<Tasks tasks={tasks} onDelete={deleteTask} onToggle={toggleRemainder}/> ):( "There is no task To show")}
  
  </div>);
};

export default TaskPage;
